import { Component, OnInit, Input, ElementRef, Output, ViewChild } from '@angular/core';
import { EventEmitter } from 'events';

@Component({
  selector: 'up-input',
  templateUrl: './input.component.html',
  styleUrls: ['./input.component.scss']
})
export class InputComponent implements OnInit {

  @Input() type: string;
  @Input() id: string;
  @Input() placeholder: string;
  @Input() localRef: string;
  @Output() localRefValue = new EventEmitter();
  @ViewChild('localRef', {static: false}) element: ElementRef;
 
  constructor() { }

  ngOnInit() {

    this.localRefValue.emit(this.element.nativeElement.value)
  }

}
